.data-source {
}

.data-source-main {
    position: fixed;
    top: calc(100vh + 50px);
    bottom: 0;
    left: 0;
    right: 0;
    background: var(--main-bg-glass-color-1);
    width: 100vw;
    transition: 0.1s;
    z-index: var(--zindex-max);
    display: flex;
    flex-direction: column;

    .head {
        width: 100vw;
        height: 50px;
        display: flex;
        flex-direction: row;

        .left {
            height: 100%;
            width: calc(30% - 5px);
            text-align: left;
            padding-left: 5px;
        }

        .mid {
            width: 40%;
            height: 100%;
            text-align: center;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: var(--font-size-mid);
            font-weight: bold;
        }

        .right {
            height: 100%;
            width: calc(30% - 5px);
            text-align: right;
            padding-right: 5px;
        }
    }

    .main {
        width: 100vw;
        height: calc(100% - 130px);
        overflow: auto;
    }

    .foot {
        width: 100vw;
        height: 80px;
        display: flex;
        justify-content: center;
        align-items: center;

        .btn {
            display: flex;
            justify-content: center;
            align-items: center;
            height: 100%;
            width: 50%;
            font-size: var(--font-size-big);
            cursor: pointer;
            color: var(--primary-color-1);
        }
    }
}

.data-source-main.open {
    backdrop-filter: var(--main-filter);
    top: 0;
}

.data-source-bottom {
    display: flex;
    justify-content: center;
    align-items: center;

    .data-source-btn {
        position: fixed;
        bottom: 25px;
        width: 60px;
        height: 60px;
        background: var(--primary-color-1);
        box-shadow: var(--normal-box-shadow);
        border-radius: 50%;
        border: 5px solid var(--primary-color-3);
        color: white;
        font-weight: bold;
        font-size: var(--font-size-mid);
        cursor: pointer;
    }
}