.event-app {
    display: flex;
    flex-direction: column;
    width: 100%;

    .event-app-item {
        height: 150px;
        flex-shrink: 0;
        padding: 10px;
        width: calc(100% - 20px);
        background: var(--main-bg-color-2);
        border-radius: var(--border-radius-1);
        margin-bottom: 10px;

        .event-head {
            width: 100%;
            height: 30px;
            display: flex;
            flex-direction: row;

            .event-head-title {
                margin-right: 10px;
                height: 100%;
                width: 40%;
                font-size: var(--font-size-small);
                font-weight: bold;
                color: var(--primary-color-1);
            }

            .event-head-datetime {
                width: 60%;
                height: 100%;
                font-size: var(--font-size-mini);
                font-weight: bold;
                color: var(--main-text-color-2);
                text-align: right;
            }
        }

        .event-content {
            width: 100%;
            height: calc(100% - 30px);
            display: flex;

            .event-detail {
                width: 60%;
                height: 100%;
                font-size: var(--font-size-small);
            }

            .event-countdown {
                width: 40%;
                height: 100%;
                display: flex;
                flex-direction: column;
                cursor: pointer;

                .event-countdown-head {
                    flex-shrink: 0;
                    height: 30px;
                    width: 100%;
                    color: var(--primary-color-1);
                    font-size: var(--font-size-small);
                    display: flex;
                    flex-direction: row;

                    .countdown-head-title {
                        width: 100%;
                    }

                    .countdown-head-operate {
                        width: 100%;
                        text-align: right;
                        cursor: pointer;
                    }
                }

                .event-countdown-content {
                    flex-shrink: 0;
                    height: calc(100% - 30px);
                    width: 100%;
                    font-size: var(--font-size-mid);
                    color: var(--primary-color-3);
                }
            }
        }
    }
}

.count-down-second {
    height: 100%;
    width: 100%;
    font-size: var(--font-size-big-1);
    color: var(--primary-color-3);
}
