.data-view {
    width: 100%;
    padding-bottom: 100px;
}

.data-type-view-item {
    width: 100%;

    .data-type-title {
        width: calc(100% - 20px);
        padding: 10px;
        font-size: var(--font-size-mid);
        font-weight: bold;
        position: sticky;
        top: 0;
        background: var(--main-bg-color-1);
    }

    .data-type-content {
        width: calc(100% - 20px);
        padding-left: 10px;
        padding-right: 10px;
    }
}

.app-view-item {
    .app-view-title {
        width: 100%;
        padding-top: 10px;
        padding-bottom: 10px;
        font-size: var(--font-size-small);
        position: sticky;
        top: 49px;
        background: var(--main-bg-color-1);
    }

    .app-view-content {
        width: 100%;
    }
}