.data-base {
    position: fixed;
    top: 100vh;
    bottom: 0;
    left: 0;
    right: 0;
    background: var(--main-bg-glass-color-1);
    width: 100vw;
    transition: 0.1s;
    z-index: var(--zindex-max);
}

.data-base.open {
    backdrop-filter: var(--main-filter);
    top: 0;
}

.data-base-head {
    width: 100%;
    height: 50px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    .head-item-left {
        width: calc(33.3% - 10px);
        height: 100%;
        padding-left: 10px;
        display: flex;
        justify-content: left;
        align-items: center;
        font-size: var(--font-size-mid);
    }

    .head-item-mid {
        width: 33.3%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: var(--font-size-mid);
        font-weight: bold;
    }

    .head-item-right {
        width: calc(33.3% - 10px);
        height: 100%;
        padding-right: 10px;
        display: flex;
        justify-content: right;
        align-items: center;
        font-size: var(--font-size-mid);
    }
}

.data-base-main {
    width: 100%;
    height: calc(100% - 50px - 80px);
    overflow: auto;
}

.data-base-item {
    width: calc(100% - 20px);
    height: 120px;
    margin: 10px;
    background: var(--main-bg-color-2);
    border-radius: var(--border-radius);
    display: flex;
    flex-direction: row;
}

.data-base-item-content {
    width: calc(100% - 120px - 20px);
    height: calc(100% - 20px);
    padding: 10px;

    .data-base-item-title {
        height: 30%;
        width: 100%;
        color: var(--primary-color-1);
        font-size: var(--font-size-small);
        font-weight: bold;
    }

    .data-base-item-info {
        height: 70%;
        width: 100%;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        gap: 10px;
        overflow-y: auto;
    }

    .data-base-info-item {
        flex-shrink: 0;
        line-height: 30px;
        height: 30px;
        padding: 3px;
        text-align: center;
        background: var(--primary-color-1);
        border: 2px solid var(--primary-color-3);
        border-radius: var(--border-radius);
        color: white;
    }
}

.data-base-item-operate {
    width: 120px;
    height: 100%;
    display: flex;
    flex-direction: row;

    .operate-item {
        flex-shrink: 0;
        height: 100%;
        width: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: var(--font-size-small);
    }
}

.data-base-foot {
    width: 100%;
    height: 80px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    .foot-item {
        width: 33.3%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        color: var(--primary-color-1);
    }
}

.edit-form {
    position: fixed;
    top: 100vh;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: auto;
    background: var(--main-bg-color-2);
    box-shadow: var(--normal-box-shadow);
    padding: 10px;

    .head {
        width: 100vw;
        height: 50px;
        display: flex;
        flex-direction: row;

        .left {
            height: 100%;
            width: calc(30% - 5px);
            text-align: left;
            padding-left: 5px;
        }

        .mid {
            width: 40%;
            height: 100%;
            text-align: center;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: var(--font-size-mid);
            font-weight: bold;
        }

        .right {
            height: 100%;
            width: calc(30% - 5px);
            text-align: right;
            padding-right: 5px;
        }
    }

    .main {
        width: 100vw;
        height: calc(100% - 130px);
        overflow: auto;
    }

    .foot {
        width: 100vw;
        height: 80px;
        display: flex;
        justify-content: center;
        align-items: center;

        .btn {
            display: flex;
            justify-content: center;
            align-items: center;
            height: 100%;
            width: 50%;
            font-size: var(--font-size-big);
            cursor: pointer;
            color: var(--primary-color-1);
        }
    }
}

.edit-form.open {
    top: 10vh;
}