.todo-app {
    height: 150px;
    padding: 10px;
    width: calc(100% - 20px);
    background: var(--main-bg-color-2);
    border-radius: var(--border-radius-1);
    display: flex;
    flex-direction: row;
}

.todo-info {
    width: 150px;
    height: 100%;
    display: flex;
    flex-direction: column;

    .todo-info-head {
        height: 15%;
        width: 100%;
        font-size: var(--font-size-small);
        color: var(--primary-color-1);
        font-weight: bold;
    }

    .todo-info-content {
        height: 70%;
        width: 100%;
        font-size: var(--font-size-big);
        color: var(--primary-color-3);
    }

    .todo-info-foot {
        height: 15%;
        width: 100%;
        cursor: pointer;

        .todo-done-title {
            color: var(--primary-color-1);
        }

        .todo-done-number {
            margin-left: 5px;
            color: var(--primary-color-2);
        }
    }
}

.todo-list {
    width: calc(100% - 150px);
    height: 100%;
    display: flex;
    flex-direction: row;
    gap: 10px;
    overflow: auto;
}

.todo-list-item {
    flex-shrink: 0;
    background: var(--primary-color-1);
    height: 100%;
    width: 60px;
    display: flex;
    flex-direction: column;
    border-radius: var(--border-radius-1);

    .todo-title {
        width: calc(100% - 6px);
        height: calc(60% - 6px);
        padding: 3px;
        color: var(--primary-color-3);
        font-weight: bold;
        font-size: var(--font-size-small);
        text-align: center;
    }

    .todo-time {
        width: 100%;
        height: 20%;
        color: white;
    }

    .todo-btn {
        width: calc(100% - 6px);
        height: calc(20% - 6px);
        background: var(--primary-color-2);
        margin: 3px;
        color: white;
        border-radius: var(--border-radius-1);
        box-shadow: var(--normal-box-shadow);
        cursor: pointer;
    }
}

.done-list {
    width: calc(100% - 20px);
    padding: 10px;

    .done-list-item {
        width: calc(100% - 10px);
        padding: 5px;
        margin-bottom: 10px;
        background: var(--main-bg-color-2);
        border-radius: var(--border-radius-1);
        display: flex;
        flex-direction: row;

        .done-info {
            width: calc(100% - 80px);
            display: flex;
            flex-direction: column;
            gap: 5px;

            .done-todo-title {
                height: 50%;
                width: 100%;
                display: flex;
                align-items: center;
                font-size: var(--font-size-small);
                font-weight: bold;
                color: var(--main-text-color-2);
            }

            .done-todo-time {
                height: 25%;
                width: 100%;
                color: var(--main-text-color-2);
            }

            .done-todo-done-time {
                height: 25%;
                width: 100%;
                color: var(--main-text-color-2);
            }
        }

        .done-operate {
            width: 80px;
            display: flex;
            flex-direction: column;
            gap: 5px;

            .done-operate-btn {
                color: var(--primary-color-3);
            }
        }
    }
}