/*layout*/
.sys-head {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    height: 50px;
    line-height: 50px;
    z-index: var(--zindex-mid);
    /*background: var(--main-bg-glass-color-1);*/
    background: var(--main-bg-color-1);
    /*backdrop-filter: var(--main-filter);*/
    display: flex;
    flex-direction: row;
}

.sys-head.active {
}

.sys-head-left {
    width: calc(30% - 10px);
    height: 100%;
    padding-left: 10px;
    font-size: 25px;
    font-weight: bold;
    text-align: left;
    color: var(--primary-color-1);
    display: flex;
    flex-direction: row;
    gap: 15px;
}

.sys-head-mid {
    width: 40%;
    height: 100%;
    font-size: var(--font-size-mid);
    font-weight: bold;
    text-align: center;
}

.sys-head-right {
    width: calc(30% - 10px);
    height: 100%;
    font-size: 25px;
    padding-right: 10px;
    color: var(--primary-color-1);
    display: flex;
    flex-direction: row;
    justify-content: right;
    gap: 25px;
}

.sys-head-right.btn {
    font-size: var(--font-size-small);
    margin-right: 5px;
    cursor: pointer;
}

.sys-main {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: auto;
    background: var(--main-bg-color-1);
}

.sys-main.all {
    padding-top: 50px;
    padding-bottom: 80px;
}

.sys-main.top {
    padding-top: 50px;
    padding-bottom: 0;
}

.sys-main.bottom {
    padding-bottom: 80px;
    padding-top: 0;
}

.sys-foot {
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    height: 80px;
    line-height: 80px;
    z-index: var(--zindex-mid);
    background: var(--main-bg-glass-color-1);
    backdrop-filter: var(--main-filter);
}

.sys-foot.active {
}

.flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

/*msg*/
.sys-msg-container {
    width: 100%;
    position: fixed;
    top: 10px;
    z-index: var(--zindex-max);
}

.sys-msg {
    width: 10px;
    height: 10px;
    border-radius: 30px;
    transition: 0.5s;
    background: black;
    border: 1px solid var(--main-text-color-1);
    box-shadow: var(--normal-box-shadow);
    opacity: 0;
}


.sys-msg.toast {
    opacity: 1;
    width: 130px;
    height: 130px;
}

.sys-msg.small {
    opacity: 1;
    width: 50vw;
    height: 30px;
}

.sys-msg.mid {
    opacity: 1;
    width: calc(100vw - 20px);
    height: 90px;
    border-radius: 35px;
}

.sys-msg.big {
    opacity: 1;
    width: calc(100vw - 20px);
    height: 150px;
}

.sys-msg-content {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    transition: 0.5s;
}

.msg-icon {
    width: 100%;
    font-size: var(--font-size-big);
    font-weight: bold;
    color: var(--success-color);
    text-align: center;
    margin-bottom: 5px;
}

.msg-text {
    width: 100%;
    font-size: var(--font-size-small);
    text-align: center;
    color: white;
}

/*form*/
.input {
    width: calc(100% - 20px);
    padding: 10px;
    height: 100px;
    display: flex;
    gap: 10px;
    flex-direction: column;
}

.input-title {
    height: 40px;
    width: 100%;
    font-size: var(--font-size-mid);
    font-weight: bold;
}

.input-content {
    height: 50%;
    width: 100%;
}

.input-content input {
    width: calc(100% - 10px - 10px);
    height: calc(100% - 10px);
    outline-style: none;
    background: var(--primary-color-3);
    border: 5px solid var(--primary-color-1);
    padding: 5px 5px;
    font-size: var(--font-size-small);
    color: var(--main-text-color-1);
}

.input-read-only {
    width: calc(100% - 10px);
    height: calc(100%);
    outline-style: none;
    background: var(--primary-color-3);
    padding: 5px 5px;
    font-size: var(--font-size-small);
    display: flex;
    align-items: center;
}

.input-content input:focus {
    box-shadow: var(--normal-box-shadow);
}

.input-content select {
    width: 100%;
    height: 100%;
    outline-style: none;
    background: var(--primary-color-3);
    border: 5px solid var(--primary-color-1);
    font-size: var(--font-size-small);
    color: var(--main-text-color-1);
}

.system-btn {
    background: var(--primary-color-1);
    width: 80px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    color: white;
    border-radius: var(--border-radius);
    cursor: pointer;
}

.sys-empty {
    width: 100%;
    height: 100%;
    min-height: 100px;
    font-size: var(--font-size-small);
    color: grey;
}

@media screen and (max-width: 768px) {
    ::-webkit-scrollbar {
        display: none;
    }
}

/*animate*/
.dh-shine {
    animation: shine 1s infinite ease-in-out;
}

@keyframes shine {
    from {
        opacity: 0;
    }
    to {
        opacity: 100;
    }
}

/*popup*/
.sys-popup {
    position: fixed;
    top: 120vh;
    bottom: 0;
    left: 0;
    right: 0;
    background: var(--main-bg-glass-color-1);
    width: 100vw;
    transition: 0.1s;
    z-index: var(--zindex-max);
    display: flex;
    flex-direction: column;

    .head {
        width: 100vw;
        height: 50px;
        display: flex;
        flex-direction: row;

        .head-left {
            height: 100%;
            width: calc(30% - 5px);
            text-align: left;
            padding-left: 5px;
        }

        .head-mid {
            width: 40%;
            height: 100%;
            text-align: center;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: var(--font-size-small);
            font-weight: bold;
        }

        .head-right {
            height: 100%;
            width: calc(30% - 5px);
            text-align: right;
            padding-right: 5px;
        }
    }

    .main {
        width: 100vw;
        height: calc(100% - 130px);
        overflow: auto;
    }

    .foot {
        width: 100vw;
        height: 80px;
        display: flex;
        justify-content: center;
        align-items: center;

        .btn {
            display: flex;
            justify-content: center;
            align-items: center;
            height: 100%;
            width: 50%;
            font-size: var(--font-size-big);
            cursor: pointer;
            color: var(--primary-color-1);
        }
    }
}

.sys-popup.show {
    backdrop-filter: var(--main-filter);
    top: 0;
}